let _this
export default {
	data() {
		return {
			userDetail: '',
			nickname: ''
		};
	},
	onLoad:function(){
		_this = this
		this.fetch()
	},	
	methods:{
		async fetch(){
			const res  = await this.$http.get('/user/details')
			console.log(res)
			this.userDetail = res.data.data
			
		},
			
		upload:function(){
			uni.chooseImage({
				count: 1,
				sizeType: ['compressed'],
			    success: (chooseImageRes) => {
			        const tempFilePaths = chooseImageRes.tempFilePaths;
			        _this.pictures = ''
							 
					  uni.uploadFile({
						  url: 'http://test.144f.com:8080/sl-web'+'/basicdata/upload', 
						  filePath: tempFilePaths[0],
						  name:'file',
						  success: async (uploadFileRes) => {
							  console.log(uploadFileRes);	
							  _this.pictures=JSON.parse(uploadFileRes.data).data.msg
							  uni.showToast({
								title: '上传成功',
								icon:'none',
								duration: 1000
							  });
							  let data ={
								  icon: _this.pictures
							  }
							  const res = await this.$http.get('/user/update_icon',{data: data})
							  console.log(res)
						  }
					 });
				}
								 
								  
			})        
		},
		submit:function(){
			uni.navigateBack({
				delta: 1
			});
		},
		toBindPhone:function(){
			uni.showToast({
				title: '待开放',
				icon: 'none',
				duration: 1000
			});
		},
		toModiPayPass:function(){
			uni.navigateTo({
				url: './modiPayPass'
			})	
		},
		toModiLoginPass:function(){
			uni.navigateTo({
				url: './modifyLoginPass',
				
			});
		},
		toMailBind:function(){
			if (this.userDetail.mail ==''){
				uni.navigateTo({
					url: './bindEmail',
					
				});
			}
			
		}
	}
}
